.outside_searchcar_boxmodel{
	background-color: rgb(252,252,252);
	padding: 100px 0px;
	margin-top: 100px;
}
.boxmodel{
	width: 70%;
	margin: auto;
	margin-top: 0px;
	border: 1px solid lightgrey;
	overflow: hidden;
	box-shadow: 2px 2px 1px #aaaaaa;
}
.boxmodel_topbar{
	display: flex;
	background-color: #f0ecec;

}
.boxmodel_title{
	margin: 0;
	background-color: var(--default-color);
	transform: skewX(-24deg);
	width: 400px;
	padding: 5px 5px 5px 30px;
	position: relative;
	right: 12px;
}

.boxmodel_title > * { 
	transform: skewX(24deg); 
}
.boxmodel_title p{
	margin: 0;
	margin-left: 10px;
	font-family: 'Bebas Neue', cursive;
	font-size: 25px;
	color: rgba(248,248,248,255);
}
.extra{
	width: 5px;
	background-color: var(--default-color);
	transform: skewX(-24deg);
	position: relative;
	right: 12px;
	margin-left: 5px; 
}
.boxmodel_nav{
	display: flex;
	align-items: center;
	margin-left: auto;
}
.boxmodel_nav a{
	margin-right: 30px;
}
.boxmodel_nav a img{
	width: 8px;
}
.boxmodel_body{
	background-color: #f8f4f4;
	padding: 10px;
	display: flex;
}

.imgresult{
	width: 60%;
	padding: 50px;
	display: flex;
	align-items: center;
	justify-content: center;
}
.imgresult img{
	width: 100%;
	height: auto;
}


/* 
  ##Device = Desktops
  ##Screen = 1281px to higher resolution desktops
  */

  @media (min-width: 1281px) {

}

/* 
##Device = Laptops, Desktops
##Screen = B/w 1025px to 1280px
*/

@media (min-width: 1025px) and (max-width: 1280px) {


    .boxmodel{
        width: 80%;
    }
}

/* 
##Device = Tablets, Ipads (portrait)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) {
    
    .boxmodel{
        width: 95%;
    }

}

/* 
##Device = Tablets, Ipads (landscape)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

   
    .boxmodel{
        width: 100%;
    }
}

/* 
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) {
    
    .imgresult{
        padding:0px;
    }
    .search_card_col {
        height: 300px;
    }
    .boxmodel{
        width: 100%;
    }
    .boxmodel_title{
        width: 300px;
    }
    .boxmodel_body {
        padding:20px;
    }
}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {
    
    .outside_searchcar_boxmodel{
        margin-top: 0px;
    }
	.boxmodel{
		width: 100%;
	}
	.boxmodel_title{
		width: 250px;
		font-size: 10px;
	}
    .boxmodel_body{
        flex-direction: column;
    }
    .imgresult{
        padding:0px;
        width: 100%;
        height: 175px;
        margin-top: 20px;
        margin-left: 20px;
        display: flex;
        align-items: center;
        justify-content: center;
    }
}